import styled from 'styled-components';
import IconFacebook from '@wikia/react-common/icons/IconFacebook.esm';
import IconTwitter from '@wikia/react-common/icons/IconTwitter.esm';
import IconLink from '@wikia/react-common/icons/IconLink.esm';
import { getCommunityName }from '../../Config';

import { CENTER_WITH_FLEX } from '../../utils/cssSnippets';

import {QuestionShapeType} from "../../types";

const ShareCardContainer = styled.div`
    background-color: ${({ theme }) => (theme.color.white)};
    border-radius: 3px;
    box-shadow: 0 4px 12px 0 rgba(0, 0, 0, 0.3);
    flex-direction: column;
    height: 310px;
    margin: auto;
    padding: 30px;
    width: 530px;
    ${CENTER_WITH_FLEX}

    @media ${({ theme }) => theme.media.small_down} {
        width: 100%;
    }
`;

const YouScored = styled.div`
    color: ${({ theme }) => (theme.color.fandom_navy)};
    font-size: ${({ theme }) => (theme.font_size.xl)};
    font-weight: ${({ theme }) => (theme.font_weight.light)};
    letter-spacing: normal;
    line-height: 1.1;
    text-align: center;
`;

const Emoji = styled.div`
    font-size: 60px;
    line-height: 1;
    padding: 6px;
    text-align: center;
`;

const Score = styled.div`
    color: ${({ theme }) => (theme.color.fandom_navy)};
    font-size: 72px; // not in rc
    font-weight: ${({ theme }) => (theme.font_weight.bold)};
    line-height: 0.83;
    margin-top: 8px;

    @media ${({ theme }) => theme.media.small_down} {
        font-size: 60px;
    }
`;

const SocialMedia = styled.div`
    align-items: flex-end;
    display: flex;
    justify-content: space-between;
    width: 100px; // used to calculate spacing in between
`;

const ShareFooter = styled.div`
    align-items: flex-end;
    display: flex;
    justify-content: space-between;
    margin-top: 60px;
`;

const IconLinkStyled = styled(IconLink)`
    cursor: pointer; 
`;

const ICON_SIZE = 21;

function isCorrect(question) {
    const correctAnswer = question.answers.find((answer) => {return answer.isCorrect});
    return correctAnswer.isSelected === true;
}
const SCORE_EMOJI = {
    UH_OH: ['😱', '😳', '💩'],
    NEUTRAL: ['👀', '🧐', '😬'],
    GOOD: ['😎', '🙌', '👌'],
    NAILED_IT: ['🔥', '🎉', '💪'],
};

function accuracyToEmoji(accuracy) {
    const rand = Math.floor(Math.random() * 3);

    if (accuracy < .25) {
        return SCORE_EMOJI.UH_OH[rand];
    }

    if (accuracy < .50) {
        return SCORE_EMOJI.NEUTRAL[rand];
    }

    if (accuracy < .75) {
        return SCORE_EMOJI.GOOD[rand];
    }

    return SCORE_EMOJI.NAILED_IT[rand];
}

function buildShareUrl(threadId) {
    return `https://${getCommunityName()}.fandom.com/f/p/${threadId}`;
}

// todo move to react-common
function copyToClipbaord(str) {
    const el = document.createElement('textarea');
    el.value = str;
    document.body.appendChild(el);
    el.select();
    document.execCommand('copy');
    document.body.removeChild(el);
}

function ShareCard({ questions, threadId }) {
    const numberCorrect = questions.reduce((accum, question) => {
        if (isCorrect(question)) {
            return accum + 1;
        }

        return accum;
    }, 0);
    const accuracy = numberCorrect / questions.length;
    const emoji = accuracyToEmoji(accuracy);


    const shareUrl = buildShareUrl(threadId);
    const facebookShareUrl = `https://www.facebook.com/sharer/sharer.php?u=${encodeURIComponent(shareUrl)}`;
    const twitterShareUrl = `https://twitter.com/share?url=${encodeURIComponent(shareUrl)}`;
    const copyLinkToClipboard = () => copyToClipbaord(shareUrl);

    return (
        <ShareCardContainer>
            <Emoji>{emoji}</Emoji>
            <YouScored>You scored</YouScored>
            <Score>{numberCorrect} / {questions.length}</Score>
            <ShareFooter>
                 <SocialMedia>
                     <a href={facebookShareUrl} target='_blank'><IconFacebook height={ICON_SIZE} fill="#063d86" /></a>
                     <a href={twitterShareUrl} target='_blank'><IconTwitter height={ICON_SIZE} fill="#063d86" /></a>
                     <IconLinkStyled onClick={copyLinkToClipboard} height={ICON_SIZE} fill="#063d86" />
                 </SocialMedia>
            </ShareFooter>
        </ShareCardContainer>
    );
}

ShareCard.propTypes = {
    threadId: PropTypes.string.isRequired,
    questions: PropTypes.arrayOf(QuestionShapeType).isRequired,
};

export default ShareCard;
